<template>
  <div
    class="buildamic-set"
    :class="[
      `buildamic-set-${field.config.statamic_settings.handle}-fieldtype`,
    ]"
  >
    <div class="admin_label mb-4">
      <element-container>
        <publish-field
          :handle="adminLabel.handle"
          :config="adminLabel"
          :meta="null"
          :value="adminLabel.value"
          @input="updateField({ path: 'admin_label', val: $event })"
        />
      </element-container>
    </div>
    <vue-tabs :id="field.uuid">
      <vue-tab name="Content" selected="selected">
        <set-field
          v-for="(val, key) in field.value"
          :key="key + field.uuid"
          :value="val"
          :handle="key"
          :field="field"
          :type="field.config.statamic_settings.handle"
          :setDefaults="setFieldDefaults"
          :fieldDefaults="fieldDefaults"
        />
      </vue-tab>
      <vue-tab name="Design">
        <design-tab :field="field" :fieldDefaults="setDefaults" />
      </vue-tab>
      <vue-tab name="Options">
        <options-tab :field="field" />
      </vue-tab>
    </vue-tabs>
  </div>
</template>

<script>
import SetField from "./SetField.vue";
import OptionsTab from "../shared/OptionsTab.vue";
import DesignTab from "../shared/DesignTab.vue";
import { createModule } from "../../factories/modules/moduleFactory";
import OptionsFields from "../../mixins/OptionsFields";
export default {
  props: {
    field: {
      type: Object,
      required: true,
    },
    fieldDefaults: Object,
  },
  mixins: [OptionsFields],
  components: {
    SetField,
    OptionsTab,
    DesignTab,
  },
  data: function () {
    return {
      adminLabel: {
        display: "Admin Label",
        placeholder: "Admin Label",
        input_type: "text",
        instructions:
          "Change the label on this module to make it easier to identify in the admin.",
        type: "text",
        icon: "text",
        handle: "admin_label",
        value: this.getDeep("admin_label") ?? null,
      },
    };
  },
  computed: {
    setFieldDefaults() {
      const test = this.fieldDefaults[
        this.field.config.statamic_settings.handle
      ].fields.reduce((acc, cur) => {
        acc[cur.handle] = cur;
        return acc;
      }, {});
      return test;
    },
  },
  methods: {
    createField(f, i) {
      return createModule("Field", {
        ADMIN_LABEL: f,
        CONFIG:
          this.fieldDefaults.sets[this.field.config.statamic_settings.handle]
            .fields[i].config,
        VALUE:
          this.fieldDefaults.sets[this.field.config.statamic_settings.handle]
            .fields[i].value,
        HANDLE: f,
        TYPE: this.fieldDefaults.sets[
          this.field.config.statamic_settings.handle
        ].fields[i].config.type,
      });
    },
  },
};
</script>
