___CSS_LOADER_EXPORT___.push([module.id, ".buildamic-field+.buildamic-field[data-v-e91d9960]{margin-top:2rem}", "",{"version":3,"sources":["webpack://./resources/js/components/fields/SetField.vue"],"names":[],"mappings":"AAsJA,mDACA,eACA","sourcesContent":["<template>\n  <div :class=\"[`${getType}-fieldtype`]\" v-if=\"showField()\">\n    <div>\n      <element-container>\n        <publish-field\n          :config=\"getConfig\"\n          :value=\"value\"\n          :meta=\"getMeta\"\n          :handle=\"handle\"\n          @input=\"handleInput\"\n          @meta-updated=\"\n            updateField({\n              obj: field,\n              path: `computed.meta.${handle}`,\n              val: $event,\n            })\n          \"\n          @focus=\"$emit('focus')\"\n          @blur=\"handleBlur\"\n        />\n      </element-container>\n      <div v-if=\"errors.length\" class=\"errors pl-3\">\n        <span v-for=\"error in errors\" :key=\"error\" class=\"text-red-600\">{{\n          error\n        }}</span>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport ColorFieldtype from \"./overrides/ColorFieldtype.vue\";\nimport OptionsFields from \"../../mixins/OptionsFields.js\";\nimport Validator from \"../../field-conditions/Validator\";\nimport { validateForm } from \"js-laravel-validation\";\n\nexport default {\n  props: {\n    field: Object,\n    handle: String,\n    type: String,\n    value: [Number, String, Array, Object],\n    fieldDefaults: {\n      type: Object,\n    },\n    setDefaults: {\n      type: Object,\n    },\n  },\n  components: {\n    ColorFieldtype,\n  },\n  mixins: [OptionsFields],\n  data() {\n    return {\n      fieldData: this.field,\n      value: null,\n      errors: [],\n    };\n  },\n  computed: {\n    sets() {\n      return [this.fieldDefaults[this.handle]];\n    },\n    getFieldDefaults() {\n      return this.setDefaults[this.handle];\n    },\n    getConfig() {\n      if (this.fieldData?.computed?.config[this.handle]) {\n        return this.fieldData?.computed.config[this.handle];\n      }\n      return this.getFieldDefaults.config;\n    },\n    getMeta() {\n      if (this.fieldData?.computed?.meta[this.handle]) {\n        return this.fieldData?.computed?.meta[this.handle];\n      }\n      return this.getFieldDefaults.meta;\n    },\n    getType() {\n      return this.getConfig.component || this.getConfig.type;\n    },\n  },\n  methods: {\n    handleInput($event) {\n      this.value = $event;\n\n      this.validateField();\n      if (this.errors.length) {\n        return;\n      }\n\n      this.updateField({\n        obj: this.field,\n        path: `value.${this.handle}`,\n        val: this.value,\n      });\n    },\n    handleBlur() {\n      this.validateField();\n      console.log(this.errors);\n      this.$emit(\"blur\");\n    },\n    validateField() {\n      this.errors = [];\n      if (!this.setDefaults[this.handle].config?.validate) {\n        return true;\n      }\n\n      const formData = {\n        [this.handle]: {\n          value: this.value,\n          validation: this.setDefaults[this.handle].config?.validate.join(\"|\"),\n        },\n      };\n\n      const result = validateForm({ formData });\n\n      //   return console.log({ result });\n\n      if (result.errors[this.handle]) {\n        this.errors.push(...result.errors[this.handle]);\n      }\n\n      return this.errors;\n    },\n    showField() {\n      const validator = new Validator(\n        this.setDefaults[this.handle].config,\n        this.field.value,\n        {},\n        \"base\"\n      );\n      return validator.passesConditions();\n    },\n  },\n  mounted() {\n    // console.log(\"handle\", this.handle);\n    // console.log(\"field\", this.fieldData);\n    // console.log(\"meta\", this.getMeta);\n  },\n  provide() {\n    return {\n      storeName: \"base\",\n    };\n  },\n};\n</script>\n\n<style scoped>\n.buildamic-field + .buildamic-field {\n  margin-top: 2rem;\n}\n</style>\n"],"sourceRoot":""}]);
